@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');

:root {
  --body-bg:#f5f5f5;
  --txt-color:#6d6d6d;
  --txt-white:#fff;
  --calc-res-color:#2f2d2f;
  --calc-btn-color:#d400fff;
  --calc-bg:#fff;
  --calc-bg-btn-color:#f0c9e7;
}

.dark {
  --calc-res-color: #fff;
  --calc-bg: #373737;
  --calc-btn-color:#fff;
  --txt-color:#fff;
  --calc-bg-btn-color:#c686b7;
}

* {
  padding:0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-family:"Montserrat" , sans-serif;
  font-weight: 400;
  line-height:1.5;

  background-color: var(--body-bg);
  color: var(--txt-color);

  display: flex;
  justify-content: center;
  padding-top:100px;
}

button {
  font-family:"Montserrat" , sans-serif;
  cursor:pointer;
}

@media only screen and (max-width: 800px) {
  body {
    display: unset;
    padding-top: 0;
  }
}